<template>
  <div class="photos">
    <my-header :child="type"></my-header>
    <div class="content">
      <div class="ph-header">
        我的相册
      </div>
      <div class="ph">
         <div class="ph-item">
            <img src="./../../assets/img/800.jpg" alt="">
            <p>我的旅游相册</p>
         </div>
         <div class="ph-item">
            <img src="./../../assets/img/802.jpg" alt="">
            <p>我的旅游相册</p>
         </div>
      </div>
    </div>
  </div>
</template>

<script>
import Header from "../header";

export default {
  name: "photos",
  components: {
    myHeader: Header
  },
  data() {
    return {
      type: "photos"
    };
  }
};
</script>

<style scoped>
.content {
  display: block;
  background-color: rgba(225,225,225,0.6)
}
.content .ph-header {
  width: 100%;
  height: 300px;
  background-image: url("./../../assets/img/800.jpg");
  background-size: 100% 100%;
  padding: 20px;
  box-sizing: border-box;
}
.content .ph {
  width:100%;
  padding: 6px;
  box-sizing: border-box;
}
.ph .ph-item{
  display:inline-block;
  width: 130px;
  height: 190px;
  padding: 3px;
  background: white;
  border:1px solid gray;
  box-sizing: border-box;
}
.content .ph img {
  width: 100%;
  height: 160px;
  /* box-shadow: */
}
.ph-item p{
  text-align: center;
}
</style>





